<template>
  <div class="blog-area">
    <div class="blog-list item-1">
      <transition-group name="transY" mode="out-in" >
        <div class="blog-item" ref="item1" :key="item.id" v-for="item in itemList1.data" @click="handleClick(item.id)">
          <div class="img" :style="`height: ${item.height}px; background-image: url(${item.banner})`"></div>
          <div class="title">{{item.title}}</div>
          <aside>{{item.aside}}</aside>
        </div>
      </transition-group>
    </div>
    <div class="blog-list item-2">
      <transition-group name="transY" mode="out-in" >
        <div class="blog-item" ref="item2" :key="item.id" v-for="item in itemList2.data" @click="handleClick(item.id)">
          <div class="img" :style="`height: ${item.height}px; background-image: url(${item.banner})`"></div>
          <div class="title">{{item.title}}</div>
          <aside>{{item.aside}}</aside>
        </div>
      </transition-group>
    </div>
    <div class="blog-list item-3">
      <transition-group name="transY" mode="out-in" >
        <div class="blog-item" ref="item3" :key="item.id" v-for="item in itemList3.data" @click="handleClick(item.id)">
          <div class="img" :style="`height: ${item.height}px; background-image: url(${item.banner})`"></div>
          <div class="title">{{item.title}}</div>
          <aside>{{item.aside}}</aside>
        </div>
      </transition-group>
    </div>
  </div>
</template>
<script>
import eventHandle from '@/utils/eventHandle'
export default {
  data () {
    return {
      loading: false,
      isEnd: false,
      itemList1: {
        height: 0,
        data: []
      },
      itemList2: {
        height: 0,
        data: []
      },
      itemList3: {
        height: 0,
        data: []
      },
      pageNum: -1,
      pageCount: 0
    }
  },
  mounted () {
    eventHandle.on('pageScroll', this.pageScroll)
    this.getList()
  },
  beforeDestroy () {
    eventHandle.remove('pageScroll', this.pageScroll)
  },
  methods: {
    pageScroll (scrollTop, height) {
      if (this.isEnd || this.loading) return
      if (height - scrollTop < 100) {
        this.getList()
      }
    },
    getList () {
      if (this.pageNum >= this.pageCount && this.pageNum !== -1) {
        this.isEnd = true
        return
      }
      this.loading = true
      this.$store.dispatch('blog/getList', {
        pageNum: this.pageNum + 1
      }).then(res => {
        this.pageNum = res.pageNum
        this.pageCount = res.pageCount
        this.buildItem(res.data)
      })
    },
    buildItem (data) {
      if (!data.length) {
        this.loading = false
        return
      }
      const item = data.shift()
      const heights = [
        {
          id: 1,
          height: (this.$refs.item1 || []).reduce((a, b) => {
            return a + b.clientHeight
          }, 0)
        },
        {
          id: 2,
          height: (this.$refs.item2 || []).reduce((a, b) => {
            return a + b.clientHeight
          }, 0)
        },
        {
          id: 3,
          height: (this.$refs.item3 || []).reduce((a, b) => {
            return a + b.clientHeight
          }, 0)
        }
      ].sort((a, b) => a.height - b.height)
      this[`itemList${heights[0].id}`].data.push(item)
      setTimeout(() => {
        this.buildItem(data)
      }, 0)
    },
    handleClick (id) {
      this.$router.push(`/detail/${id}`)
    }
  }
}
</script>
<style lang="stylus">

</style>



