/**
 * 简单事件监听者
 * @Author   yansanmu
 * @DateTime 2018-01-29
 */
function EventHandle () {
  this.eventMap = {}
  // 绑定this指向，防止外部更改
  this.on = this.on.bind(this)
  this.remove = this.remove.bind(this)
  this.emit = this.emit.bind(this)
}

EventHandle.prototype.on = function (eventName, callback) {
  if (!this.eventMap[eventName]) {
    this.eventMap[eventName] = []
  }
  this.eventMap[eventName].push(callback)
  return this.remove.bind(null, eventName, callback)
}

EventHandle.prototype.remove = function (eventName, callback) {
  const eventList = this.eventMap[eventName]
  const index = eventList.findIndex(d => d === callback)
  if (~index) {
    eventList.splice(index, 1)
  }
}

EventHandle.prototype.emit = function (eventName) {
  const eventList = this.eventMap[eventName] || []
  eventList.forEach(d => {
    d.apply(null, Array.prototype.slice.call(arguments, 1))
  })
}

const eventHandle = new EventHandle()

// 输出实例
export default eventHandle

// 输出构造函数  方便生成新实例
export {EventHandle}



