export default {
  namespaced: true,
  state: {
    list: {
      pageNum: 0,
      pageCount: 0,
      data: []
    }
  },
  mutations: {
    setList (state, data) {
      state.list = data
    }
  },
  actions: {
    getList ({state, commit}, opts) {
      const list = []
      for (let i = 0; i < 10; i++) {
        list.push({
          id: opts.pageNum * 10 + i,
          title: 'The application of white space in design. The application of white',
          aside: '2018.01.01 by Areswen',
          banner: './static/blog/bg.svg',
          height: Math.floor(Math.random() * 160) + 240
        })
      }
      return new Promise((resolve) => {
        resolve({
          pageNum: opts.pageNum,
          pageCount: 10,
          data: list
        })
      })
    }
  }
}



