const demoList = [
  {
    title: [
      'THE RICE OF ', 'MASTER BRADNING ', 'INDENTITY'
    ],
    icon: './static/works/icon1.svg',
    iconb: './static/works/icon1-b.svg',
    background: './static/works/bg-1.svg'
  },
  {
    title: [
      'CHIC MOMENT ', 'BRANDING ', 'INDENTITY', 'DESIGN'
    ],
    icon: './static/works/icon1.svg',
    iconb: './static/works/icon1-b.svg',
    background: './static/works/bg-2.svg'
  },
  {
    title: [
      'CHIC MOMENT ', 'BRANDING INDENTITY ', 'DESIGN'
    ],
    icon: './static/works/icon2.svg',
    iconb: './static/works/icon2-b.svg',
    background: './static/works/bg-1.svg'
  },
  {
    title: [
      'THE RICE OF ', 'BRADNING ', 'INDENTITY'
    ],
    icon: './static/works/icon2.svg',
    iconb: './static/works/icon2-b.svg',
    background: './static/works/bg-2.svg'
  }
]

export default {
  namespaced: true,
  state: {
    list: {
      pageNum: 0,
      pageCount: 0,
      total: 0,
      data: []
    }
  },
  mutations: {
    setList (state, data) {
      state.list = data
    }
  },
  actions: {
    getList ({commit}, opts) {
      const {
        pageNum,
        pageCount
      } = opts
      const list = []
      for (let i = 0; i < pageCount; i++) {
        list.push({
          id: (pageNum - 1) * pageCount + i,
          ...demoList[Math.floor(Math.random() * 4)]
        })
      }
      return new Promise((resolve) => {
        resolve({
          ...opts,
          total: 52,
          data: list
        })
      }).then((d) => {
        commit('setList', d)
      })
    }
  }
}



