<template>
  <div :class="{
    'z-menu': true,
    'is-white': !!white
  }">
    <div class="container">
      <div class="logo"></div>
      <menu-item label="HOME" active="index" path="/"></menu-item>
      <menu-item label="WORKS" active="works" path="/works?type=all">
        <div class="z-menu-child">
          <menu-item label="All" active="all" isQuery="true" path="/works?type=all"></menu-item>
          <menu-item label="Branding" active="branding" isQuery="true" path="/works?type=branding"></menu-item>
          <menu-item label="Web" active="web" isQuery="true" path="/works?type=web"></menu-item>
          <menu-item label="App" active="app" isQuery="true" path="/works?type=app"></menu-item>
          <menu-item label="Illustration" active="illustration" isQuery="true" path="/works?type=illustration"></menu-item>
        </div>
      </menu-item>
      <menu-item label="BLOG" active="blog" path="/blog?type=all">
        <div class="z-menu-child">
          <menu-item label="All" active="all" isQuery="true" path="/blog?type=all"></menu-item>
          <menu-item label="Article" active="article" isQuery="true" path="/blog?type=article"></menu-item>
          <menu-item label="Translation" active="translation" isQuery="true" path="/blog?type=translation"></menu-item>
          <menu-item label="Tutorial" active="tutorial" isQuery="true" path="/blog?type=tutorial"></menu-item>
        </div>
      </menu-item>
      <menu-item label="CONTACT" active="contact" path="/contact"></menu-item>
      <div class="main-lang-box">简体中文</div>
    </div>
  </div>
</template>
<script>
import MenuItem from './MenuItem'
export default {
  props: ['white'],
  components: {MenuItem}
}
</script>
<style lang="stylus">
  .z-menu
    position absolute
    top 0
    left 0
    right 0
    height 160px
    z-index 2
    text-align right
  .z-menu-child
    position absolute
    top 100%
    left 0
    white-space nowrap
  .logo
    margin-top 50px
    float left
</style>



