<template>
  <div class="detail-area">
    <div v-html="html"></div>
  </div>
</template>
<script>
import axios from 'axios'
import {markdown} from 'markdown'
export default {
  data () {
    return {
      html: ''
    }
  },
  mounted () {
    axios({
      url: '/static/blog/demo.md'
    }).then(d => {
      const html = markdown.toHTML(d.data)
      this.html = html
    })
  }
}
</script>
<style lang="stylus">

</style>



