import Vue from 'vue'
import Router from 'vue-router'
import MainLayout from '@/page/main.layout'

import PageIndex from '@/page/index.page'
import PageWorks from '@/page/works.page'
import PageBlog from '@/page/blog.page'
import PageDetail from '@/page/detail.page'
import PageContact from '@/page/contact.page'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: MainLayout,
      children: [
        {
          path: '/',
          name: 'index',
          component: PageIndex
        },
        {
          path: '/works',
          name: 'works',
          component: PageWorks
        },
        {
          path: '/blog',
          name: 'blog',
          component: PageBlog
        },
        {
          path: '/detail/:id',
          name: 'detail',
          component: PageDetail
        },
        {
          path: '/contact',
          name: 'contact',
          component: PageContact
        }
      ]
    }
  ]
})



