<template>
  <div :class="{
    'z-layout': true,
    [$route.name]: !!$route.name
  }" @scroll="handleScroll">
    <div class="z-layout-inner" ref="inner">
      <Menu />
      <transition name="fade" mode="out-in" >
        <router-view></router-view>
      </transition>
      <Footer />
    </div>
  </div>
</template>
<script>
import Menu from '@/components/Menu'
import Footer from '@/components/Footer'
import eventHandle from '@/utils/eventHandle'
export default {
  components: {Menu, Footer},
  methods: {
    handleScroll (e) {
      eventHandle.emit('pageScroll', e.target.scrollTop, this.$refs.inner.clientHeight - e.target.clientHeight)
    }
  }
}
</script>
<style lang="stylus">
  /**
   * 过场动画
   */
  .fade-enter-active
    transition opacity .5s

  .fade-leave-active
    transition opacity .5s

  .fade-enter,
  .fade-leave-to /* .fade-leave-active in below version 2.1.8 */
    opacity 0

  .transY-enter-active
    transition opacity .5s

  .transY-leave-active
    transition opacity .5s

  .transY-enter,
  .transY-leave-to /* .fade-leave-active in below version 2.1.8 */
    opacity 0
    transform translateY(30px)
</style>



