          <p>if lack of creation,products lose their lives.</p>
        </div>
        <div class="item">
          <i class="b-2"></i>
          <h2>UI&UE Design</h2>
          <p>Design is the possibility of pursuit of new.If lack of</p>
          <p>invention,</p>
          <p>the design loses its value if lack of creation</p>
        </div>
        <div class="item">
          <i class="b-3"></i>
          <h2>Illustration</h2>
          <p>Design is the possibility of pursuit of new.If lack of </p>
          <p>invention,the design loses its value</p>
          <p>if lack of creation,</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Menu from '@/components/Menu'
import Footer from '@/components/Footer'
export default {
  computed: {
    ...mapState(['windowWidth'])
  },
  components: {Menu, Footer}
}
</script>
<style lang="stylus">
  /**
   * 过场动画
   */
  .home-area
    overflow hidden
    .home-right-side
      transition .45s
</style>



