<template>
  <div class="works-area">
    <div class="container">
      <div class="works-item" :key="`${$route.query.type}_${item.id}`" v-for="item in list.data">
        <div class="icon-area">
          <div class="icon" :style="`background-image: url(${item.icon})`"></div>
          <div class="iconb" :style="`background-image: url(${item.iconb})`"></div>
        </div>
        <div class="title">
          <p v-for="(d, idx) in item.title" :key="idx">{{d}}</p>
        </div>
        <div class="btn">VIEW MORE</div>
        <div class="bg">
          <div :style="`background-image: url(${item.background})`"></div>
        </div>
      </div>
      <el-pagination
        class="works-pagination"
        layout="pager, prev, next"
        :total="list.total"
        :page-size="list.pageCount"
        :current-page="list.pageNum"
        @current-change="handleChange">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState('work', ['list'])
  },
  mounted () {
    this.$store.dispatch('work/getList', {
      pageNum: 1,
      pageCount: 4
    })
  },
  methods: {
    handleChange (val) {
      this.$store.dispatch('work/getList', {
        pageNum: val,
        pageCount: 4
      })
    }
  }
}
</script>
<style lang="stylus"></style>



