<template>
  <div class="menu-item-box">
    <div :class="{
      'menu-item': true,
      'is-query': isQuery,
      'active': !isQuery ? active === $route.name : active === $route.query.type
    }" @click="handleClick">{{label}}</div>
    <transition name="fade" mode="out-in" >
      <slot v-if="active === $route.name"></slot>
    </transition>
  </div>
</template>
<script>
export default {
  props: ['label', 'active', 'path', 'isQuery'],
  methods: {
    handleClick () {
      if (this.path) {
        this.$router.push(this.path)
      }
    }
  }
}
</script>
<style lang="stylus">
  .menu-item-box
    display inline-block
    position relative
  .is-white
    .menu-item
      color #fff
      &:before
        border-right-color #fff
        border-bottom-color #fff
  .menu-item
    display inline-block
    text-align center
    font-size 18px
    color #999999
    font-weight 700
    margin-right 90px
    margin-top 88px
    position relative
    cursor pointer
    &.is-query
      font-weight 200
      margin-top 24px
      margin-right 46px
      font-size 16px
    &:before
      content ''
      position absolute
      bottom 6px
      left -24px
      border 6px solid #999
      border-left-color transparent
      border-top-color transparent
      transition .4s
      opacity 0
      transform translate(-5px, -5px)
    &:hover
      &:before
        transform translate(0, 0)
        opacity 1
    &.active
      color #000
      &:before
        transform translate(0, 0)
        opacity 1
        border-color #000
        border-left-color transparent
        border-top-color transparent
        color #000
</style>



